import tuiButton from "@/components/extend/button/button"
export default {
	components: {
		tuiButton
	},
	data() {
		return {
			orderStatus:1,//1：已支付，2:已取消，0：已关闭
		}
	},
	onLoad: function(options) {
		console.log(JSON.stringify(options.orderStatus))
		this.orderStatus = options.orderStatus;
		uni.setNavigationBarTitle({
			title:this.orderStatus==1?"支付成功":''
		});
		
	},
	/*自定义过滤器*/
	filters: {
		filterstatus: function(value) {
			switch (value) {
				case '1':
					{
						return '已支付'
						break;
					}
				case '2':
					{
						console.log(value)
						return '已取消'
						break;
					}
				case '0':
					{
						return '已关闭'
						break;
					}
			}
	
		}
	},
	methods: {
		go(page) {
			if (page == 1) {
				uni.switchTab({
					url: "/pages/mall/mall"
				})
			} else {
				uni.navigateTo({
					url: '../myOrder/myOrder'
				})
			}
		}
	}
}
